html{
    background-color: var(--black);
}

html::after{
    content: "";
    background-image: url(bg.jpg);
    background-size: cover; 
    opacity: 0.8;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1; 
}


